import React, { useState } from 'react'
import app from '../firebase_config'
import { getDatabase, ref, set, push } from 'firebase/database'
import { useNavigate } from 'react-router-dom';
import 'toastr/build/toastr.min.css';
import Swal from 'sweetalert2';


function WriteProduct() {
    const navigate = useNavigate();

    let [inputValue1, setInputValue1] = useState("");
    let [inputValue2, setInputValue2] = useState("");
    let [inputValue3, setInputValue3] = useState("");
    let [inputValue4, setInputValue4] = useState("");
    let [inputValue5, setInputValue5] = useState("");

    const saveData = async () => {
        const db = getDatabase(app);
        const newDocRef = push(ref(db, "product"));
        set(newDocRef, {
            product_name: inputValue1,
            unit: inputValue2,
            price: inputValue3 ? parseInt(inputValue3.replace(/[^0-9]/g, ''), 10) : 0,
            stock_quantity: inputValue4 ? parseInt(inputValue4.replace(/[^0-9]/g, ''), 10) : 0,
            note: inputValue5
        }).then(() => {
            // toastr.success('Cp nht thnh cng!');
            Swal.fire({
                title: 'To thnh cng!',
                icon: 'success',
                showConfirmButton: false, // Khng hin th nt OK
                timer: 2000 // ng thng bo sau 3 giy
              });              
            navigate("/");
        }).catch((error) => {
            alert("error: ", error.message);
        });
    }

    const formatNumber = (number) => {
        return new Intl.NumberFormat('vi-VN').format(number);
    };

    const handleInputChange = (event, setter) => {
        const value = event.target.value.replace(/[^0-9]/g, '');
        setter(value);
    };

    return (
        <div class="inputBox">
            <div class="header">To sn phm</div>
            <input
                type="text"
                value={inputValue1}
                placeholder='Tn sn phm'
                onChange={(e) => setInputValue1(e.target.value)}
            />
            <select
                id="unit"
                value={inputValue2}
                onChange={(e) => setInputValue2(e.target.value)}
            >
                <option value="">Chn n v</option>
                <option value="Ci">Ci</option>
                <option value="Mt">Mt</option>
                <option value="Cn">Cn</option>
                <option value="Cun">Cun</option>
                <option value="Tup">Tup</option>
                <option value="Tup">L</option>
            </select>
            <input
                type="text"
                value={inputValue3 ? formatNumber(inputValue3) : ''}
                placeholder='Gi'
                onChange={(e) => handleInputChange(e, setInputValue3)}
            />
            <input
                type="text"
                value={inputValue4 ? formatNumber(inputValue4) : ''}
                placeholder='S lng'
                onChange={(e) => handleInputChange(e, setInputValue4)}
            />
            <input
                type="text"
                value={inputValue5}
                placeholder='Ghi ch'
                onChange={(e) => setInputValue5(e.target.value)}
            />
            <div class="twoBtn">
                <button onClick={() => navigate("/")}>Quay li</button>
                <button className='writeBtn' onClick={saveData}>Lu</button>
            </div>
        </div>
    );
}

export default WriteProduct;
