import React, { useState } from 'react'
import app from '../firebase_config'
import { getDatabase, ref, set, push } from 'firebase/database'
import { useNavigate } from 'react-router-dom';
import 'toastr/build/toastr.min.css';
import Swal from 'sweetalert2';


function WriteBill() {
    const navigate = useNavigate();

    let [inputValue1, setInputValue1] = useState("");
    let [inputValue2, setInputValue2] = useState("");
    let [inputValue3, setInputValue3] = useState("");
    let [inputValue4, setInputValue4] = useState("");
    let [inputValue5, setInputValue5] = useState("");
    let [inputValue6, setInputValue6] = useState("");

    const saveData = async () => {
        const db = getDatabase(app);
        const newDocRef = push(ref(db, "bill"));
        set(newDocRef, {
            customer_name: inputValue1,
            phone: inputValue2,
            address: inputValue3,
            date: inputValue4,
            status: inputValue5,
            note: inputValue6
        }).then(() => {
            Swal.fire({
                title: 'To thnh cng!',
                icon: 'success',
                showConfirmButton: false, // Khng hin th nt OK
                timer: 2000 // ng thng bo sau 3 giy
            });
            navigate("/manageBill");
        }).catch((error) => {
            alert("error: ", error.message);
        });
    }

    const handleInputChange = (e) => {
        const rawValue = e.target.value.replace(/\D/g, ''); // Loi b cc k t khng phi s
        setInputValue2(rawValue);
    };

    const formatPhoneNumber = (number) => {
        return number.replace(/(\d{4})(\d{3})(\d{3})/, '$1 $2 $3');
    }

    return (
        <div class="inputBox">
            <div class="header">To ha n</div>
            <input
                type="text"
                value={inputValue1}
                placeholder='Tn khch hng'
                onChange={(e) => setInputValue1(e.target.value)}
            />
            <input
                type="text"
                value={formatPhoneNumber(inputValue2)}
                placeholder="in thoi"
                onChange={handleInputChange}
            />
            <input
                type="text"
                value={inputValue3}
                placeholder='a ch'
                onChange={(e) => setInputValue3(e.target.value)}
            />
            <div>
                <input
                    type="date"
                    value={inputValue4}
                    placeholder="Ngy"
                    onChange={(e) => setInputValue4(e.target.value)}
                />
                <span> Ngy  chn: {inputValue4 ? inputValue4.split('-').reverse().join('/') : ''}</span>
            </div>
            <input
                type="text"
                value={inputValue6}
                placeholder='Ghi ch'
                onChange={(e) => setInputValue6(e.target.value)}
            />
            <div class="statusBtn">
                <button onClick={() => setInputValue5(!inputValue5)}>i trng thi</button>
                <span> Ha n {inputValue5 ? ' thanh ton' : 'cha thanh ton'}</span>
            </div>
            <div class="twoBtn">
                <button onClick={() => navigate("/manageBill")}>Quay li</button>
                <button className='writeBtn' onClick={saveData}>Lu</button>
            </div>
        </div>
    );
}

export default WriteBill;
